import {Col, Nav} from 'react-bootstrap';
import {Link, NavLink} from 'react-router-dom';

import Logo from '../Logo';
import './styles.scss';
import Socials from "../Socials";
import { routes } from '../../constants/headerRoutes';
import HeaderDropdown from "../HeaderDropdown";

const Footer = props => {
    return (
        <footer className="footer">
            <div className="footer__bg" />
            <div className="footer__content w-100 d-flex flex-column flex-lg-row">

                <div className={'d-flex flex-column footer__about'}>
                    <div className="footer__logo">
                        <Logo />
                    </div>
                    <div className="d-none d-lg-flex flex-column">
                        <Socials/>
                        <span className="footer__copyright">
                            Copyright &copy; 2020, <a href="#" className={'footer__copyright-link'}>Stealth</a>
                        </span>
                    </div>
                </div>
                <div className={'footer__list-container'}>
                    <div className="d-flex footer__list">

                        {Object.keys(routes).map(key => {

                            const r = routes[key];
                            return (
                                <ul className="footer__links" key={key}>
                                    <li className={'footer__item'}><Link to={r.hasOwnProperty('path') ? r.path : '#'} className={'footer__link footer__link--primary'}>{r.title}</Link></li>
                                    {r.hasOwnProperty('routes') && Object.values(r.routes).map((item, index) => {
                                        return (
                                            <li className={'footer__item'}  key={`${key}-${index}`}>
                                                <Link to={item.path} className={'footer__link'}>{item.title}</Link>
                                            </li>
                                        )
                                    })}
                                </ul>
                            )
                        })}
                    </div>
                </div>

                <div className="d-flex d-lg-none flex-column align-items-center justify-content-center footer__mobile-socials">
                    <Socials/>
                    <span className="footer__copyright">
                            Copyright &copy; 2020, <a href="#" className={'footer__copyright-link'}>Stealth</a>
                        </span>
                </div>
            </div>
        </footer>
    )
}

export default Footer;